import React from 'react';

function BidderDashboard() {
  const endTime = new Date("2026-01-10T12:00:00");
  const remaining = Math.max(0, (endTime - new Date()) / 1000);

  return (
    <div>
      <h2>Bidder Dashboard</h2>
      <p>Remaining Time: {remaining} seconds</p>
    </div>
  );
}
export default BidderDashboard;