import React, { useState, useEffect } from 'react';
import './AuctionCard.css';

const AuctionCard = ({ auction, onBid, isLive = false, userRole }) => {
  const [timeLeft, setTimeLeft] = useState('');
  const [currentBid, setCurrentBid] = useState(auction.currentBid ?? auction.startingPrice ?? 0);
  const [bidAmount, setBidAmount] = useState('');

  // Calculate time remaining
  useEffect(() => {
    const calculateTimeLeft = () => {
      const now = new Date().getTime();
      const endTime = new Date(auction.endTime).getTime();
      const distance = endTime - now;

      if (distance > 0) {
        const days = Math.floor(distance / (1000 * 60 * 60 * 24));
        const hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
        const minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
        const seconds = Math.floor((distance % (1000 * 60)) / 1000);

        if (days > 0) {
          setTimeLeft(`${days}d ${hours}h ${minutes}m`);
        } else if (hours > 0) {
          setTimeLeft(`${hours}h ${minutes}m ${seconds}s`);
        } else {
          setTimeLeft(`${minutes}m ${seconds}s`);
        }
      } else {
        setTimeLeft('Ended');
      }
    };

    calculateTimeLeft();
    const timer = setInterval(calculateTimeLeft, 1000);

    return () => clearInterval(timer);
  }, [auction.endTime]);

  useEffect(() => {
    if (typeof auction.currentBid === 'number') {
      setCurrentBid(auction.currentBid);
    }
  }, [auction.currentBid]);

  const handleBid = () => {
    if (bidAmount && parseFloat(bidAmount) > currentBid) {
      onBid(auction.id, parseFloat(bidAmount));
      setBidAmount('');
    }
  };

  const getStatusColor = () => {
    const now = new Date();
    const startTime = new Date(auction.startTime);
    const endTime = new Date(auction.endTime);

    if (now < startTime) return '#ffc107'; // Upcoming - yellow
    if (now >= startTime && now <= endTime) return '#28a745'; // Live - green
    return '#dc3545'; // Ended - red
  };

  const getStatusText = () => {
    const now = new Date();
    const startTime = new Date(auction.startTime);
    const endTime = new Date(auction.endTime);

    if (now < startTime) return 'Upcoming';
    if (now >= startTime && now <= endTime) return 'Live';
    return 'Ended';
  };

  return (
    <div className="auction-card">
      <div className="auction-header">
        <h3>{auction.productName}</h3>
        <span
          className="auction-status"
          style={{ backgroundColor: getStatusColor() }}
        >
          {getStatusText()}
        </span>
      </div>

      <div className="auction-details">
        <div className="auction-info">
          <p><strong>Starting Price:</strong> ${auction.startingPrice}</p>
          <p><strong>Current Bid:</strong> ${currentBid}</p>
          <p><strong>Start Time:</strong> {new Date(auction.startTime).toLocaleString()}</p>
          <p><strong>End Time:</strong> {new Date(auction.endTime).toLocaleString()}</p>
          <p><strong>Time Left:</strong> {timeLeft}</p>
        </div>

        {userRole === 'BIDDER' && getStatusText() === 'Live' && (
          <div className="bid-section">
            <input
              type="number"
              placeholder="Enter bid amount"
              value={bidAmount}
              onChange={(e) => setBidAmount(e.target.value)}
              min={currentBid + 1}
              step="0.01"
            />
            <button onClick={handleBid} disabled={!bidAmount || parseFloat(bidAmount) <= currentBid}>
              Place Bid
            </button>
          </div>
        )}
      </div>
    </div>
  );
};

export default AuctionCard;
