import React, { useState, useEffect } from 'react';
import { BrowserRouter, Routes, Route, Navigate, useNavigate } from 'react-router-dom';
import AdminDashboard from './components/AdminDashboard';
import SellerDashboard from './components/SellerDashboard';
import BidderDashboard from './components/BidderDashboard';
import Login from './pages/Login';
import AuctionDetails from './pages/AuctionDetails';
import './App.css';
import Signup from './pages/Signup';
import ForgotPassword from './pages/ForgotPassword';
import Home from './pages/Home';

// Protected Route Component
function ProtectedRoute({ children, allowedRoles }) {
  const token = localStorage.getItem('token');
  const role = localStorage.getItem('role');

  if (!token) {
    return <Navigate to="/login" replace />;
  }

  if (allowedRoles && !allowedRoles.includes(role)) {
    // Redirect to appropriate dashboard based on role
    switch (role) {
      case 'ADMIN':
        return <Navigate to="/admin" replace />;
      case 'SELLER':
        return <Navigate to="/seller" replace />;
      case 'BIDDER':
        return <Navigate to="/bidder" replace />;
      default:
        return <Navigate to="/login" replace />;
    }
  }

  return children;
}

// App Content Component (to use useNavigate)
function AppContent() {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [userRole, setUserRole] = useState('');
  const navigate = useNavigate();

  useEffect(() => {
    const token = localStorage.getItem('token');
    const role = localStorage.getItem('role');

    if (token && role) {
      setIsAuthenticated(true);
      setUserRole(role);
    } else {
      setIsAuthenticated(false);
      setUserRole('');
    }
  }, []);

  const handleLogout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('role');
    localStorage.removeItem('userId');
    setIsAuthenticated(false);
    setUserRole('');
    navigate('/login');
  };

  return (
    <div className="app">
      <header className="app-header">
        <div className="header-content">
          <h1 className="app-title">Bidspark E-Auction</h1>
          <div className="header-actions">
            {isAuthenticated ? (
              <>
                <span className="user-role">Role: {userRole}</span>
                <button className="logout-btn" onClick={handleLogout}>
                  Logout
                </button>
              </>
            ) : (
              <>
                <a href="/home" className="logout-btn" style={{ backgroundColor: '#17a2b8' }}>Home</a>
                <a href="/login" className="logout-btn">Login</a>
                <a href="/signup" className="logout-btn" style={{ backgroundColor: '#28a745' }}>Sign Up</a>
              </>
            )}
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="app-main">
        <Routes>
          {/* Public Routes */}
          <Route path="/home" element={<Home />} />
          <Route path="/login" element={<Login />} />
          <Route path="/signup" element={<Signup />} />
          <Route path="/forgot-password" element={<ForgotPassword />} />

          {/* Protected Routes */}
          <Route
            path="/admin"
            element={
              <ProtectedRoute allowedRoles={['ADMIN']}>
                <AdminDashboard />
              </ProtectedRoute>
            }
          />
          <Route
            path="/seller"
            element={
              <ProtectedRoute allowedRoles={['SELLER']}>
                <SellerDashboard />
              </ProtectedRoute>
            }
          />
          <Route
            path="/bidder"
            element={
              <ProtectedRoute allowedRoles={['BIDDER']}>
                <BidderDashboard />
              </ProtectedRoute>
            }
          />
          <Route
            path="/auction/:id"
            element={
              <ProtectedRoute allowedRoles={['ADMIN', 'SELLER', 'BIDDER']}>
                <AuctionDetails />
              </ProtectedRoute>
            }
          />

          {/* Default Route */}
          <Route
            path="/"
            element={
              isAuthenticated ? (
                <Navigate
                  to={
                    userRole === 'ADMIN' ? '/admin' :
                    userRole === 'SELLER' ? '/seller' : '/bidder'
                  }
                  replace
                />
              ) : (
                <Navigate to="/home" replace />
              )
            }
          />

          {/* Catch all route */}
          <Route path="*" element={<Navigate to="/" replace />} />
        </Routes>
      </main>
    </div>
  );
}

function App() {
  return (
    <BrowserRouter>
      <AppContent />
    </BrowserRouter>
  );
}

export default App;
